import React, { useState } from 'react';
import './SectionThree.css';
import Logo from '../../svg/лого.svg';
const SectionThree = () => {
  const array = [
    'https://www.youtube.com/embed/bd-KpqIyLUk',
    'https://www.youtube.com/embed/l5vMacnir7E',
    'https://www.youtube.com/embed/X4UJ36mcSdk',
  ];
  const [video, setvideo] = useState(
    'https://www.youtube.com/embed/bd-KpqIyLUk'
  );
  const [class1, setclass1] = useState('green');
  const [class2, setclass2] = useState('blue');
  const [class3, setclass3] = useState('blue');
  const first = () => {
    setvideo(array[0]);
    setclass1('green');
    setclass2('blue');
    setclass3('blue');
  };
  const second = () => {
    setvideo(array[1]);
    setclass1('blue');
    setclass2('green');
    setclass3('blue');
  };
  const third = () => {
    setvideo(array[2]);
    setclass1('blue');
    setclass2('blue');
    setclass3('green');
  };
  return (
    <section class='section3'>
      <div class='videoReview'>
        <p>Видеоотзывы</p>
      </div>
      <iframe
        width='560'
        height='315'
        src={video}
        frameborder='0'
        allow='accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture'
        allowfullscreen
        className='videoVideo'
        id='videoYoutube'
        title='Some title'
      ></iframe>
      <div class='newClass'>
        <div className={class1} onClick={first}></div>
        <div className={class2} onClick={second}></div>
        <div className={class3} onClick={third}></div>
      </div>
      <div className='logoContaniner'>
        <img src={Logo} className='imgSloy1' alt='' />
      </div>
    </section>
  );
};

export default SectionThree;
