import React from 'react';
import { Keyframes, animated, config } from 'react-spring/renderprops';
import delay from 'delay';
// import './AnimationSlider.css';
const Content = Keyframes.Spring(async (next) => {
  // None of this will cause React to render, the component renders only once :-)
  while (true) {
    await next({
      from: { opacity: 1 },
      opacity: 1,
      left: '10%',
    });
    await delay(4000);
    await next({
      from: { opacity: 1 },
      opacity: 0,
    });

    await next({
      from: { left: '10%' },
      left: '100%',
    });
    await delay(2200);
    await next({
      from: { left: '100%', opacity: 1 },
      left: '10%',
      opacity: 1,
      config: { duration: 2000 },
    });
    await delay(1000);
  }
});

export default class First extends React.Component {
  render() {
    return (
      <div>
        <Content native>
          {(props) => (
            <animated.div
              className='slider1'
              style={{
                background: `url(${require('../../img/animation1.png')})`,
                position: 'absolute',
                ...props,
              }}
            />
          )}
        </Content>
      </div>
    );
  }
}
