import React from 'react';
import { Keyframes, animated, config } from 'react-spring/renderprops';
import delay from 'delay';

const ContentTwo = Keyframes.Spring(async (next) => {
  while (true) {
    await delay(4000);
    await next({
      from: {
        opacity: 1,
        left: '100%',
      },
      opacity: 1,
      left: '10%',
      config: { duration: 2000 },
    });

    await delay(4000);
    await next({
      from: { opacity: 0 },
      opacity: 0,
    });
    next({
      from: { left: '10%' },
      left: '100%',
    });
  }
});

export default class First extends React.Component {
  render() {
    return (
      <>
        <ContentTwo native>
          {(props) => (
            <animated.div
              className='slider2'
              style={{
                background: `url(${require('../../img/animation2.png')})`,
                position: 'absolute',
                ...props,
              }}
            />
          )}
        </ContentTwo>
      </>
    );
  }
}
